
.header[data-v-600fd0f4] {
  height: 5vh;
  background-color: #c70c0c;
}
.fade-enter-active[data-v-600fd0f4], .fade-leave-active[data-v-600fd0f4] {
  -webkit-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.fade-enter[data-v-600fd0f4], .fade-leave-active[data-v-600fd0f4] {
  opacity: 1;
  -webkit-transform: translateX(100vw);
          transform: translateX(100vw);
}
.go-player[data-v-600fd0f4]{
	float: right;
	margin-right: 5px;
}
.go-player .iconfont[data-v-600fd0f4]{
	font-size: 1.7rem;
	color: #fff;
}



