
.music-menu[data-v-555ebd0d]{
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	overflow: hidden;
}
.music-menu>.mask-bg[data-v-555ebd0d]{
	width: 120%;
	height: 120%;
	position: absolute;
	background-size: contain;
	top: -10%;
	left: -10%;
	z-index: -1;
	-webkit-filter: blur(20px) grayscale(0.5);
	filter: blur(20px) grayscale(0.5);
}
.movein-enter-active[data-v-555ebd0d],.movein-leave-active[data-v-555ebd0d]{
	-webkit-transition: all .5s;
	transition: all .5s;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}
.movein-enter[data-v-555ebd0d],.movein-leave-active[data-v-555ebd0d]{
	opacity: 1;
	-webkit-transform: translateX(100vw);
	        transform: translateX(100vw);
}
.header[data-v-555ebd0d]{
	height: 5vh;
	text-align: center;
	position: relative;
}
.header>.back[data-v-555ebd0d]{
	color: #fff;
	position: absolute;
	left: 1rem;
}
.header>.back>.iconfont[data-v-555ebd0d]{
	font-size: 1.4rem;
}
.header>.header-title[data-v-555ebd0d]{
	line-height: 5vh;
	font-size: 1.2rem;
	color: #fff;
}
.main[data-v-555ebd0d]{
	width: 100vw;
	height: 12rem;
	margin-top: 1rem;
	padding: 0 1rem;
	color: #fff;
	overflow: hidden;
}
.main>.cover[data-v-555ebd0d]{
	width: 10rem;
	height: 10rem;
	float: left;
	margin-right: 1rem;
}
.main>.title[data-v-555ebd0d]{
	padding-right: 2rem;
	font-size: 1rem;
}
.main>.creator[data-v-555ebd0d]{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.main>.creator>.creator-avatar[data-v-555ebd0d]{
	width: 2rem;
	height: 2rem;
	border-radius: 50%;
	margin-right: 0.5rem;
}



